var _this;
var graceChecker = require("../../components/graceChecker/graceChecker.js");
export default {
	data() {
		return {
			lsDisabled: false,
			UserInfoArray: {
				user_id: '',
				user_name: '',
				user_cardid: '',
				user_tel: '',
				user_point: '',
				date_entry: '',
				user_sex: '',
				user_type: '',
				user_wechat_apartment: '',
				user_main: ''
			},
			SubmitButtonDisabled: this.SubmitButtonDisabled, //提交按钮disabled状态
			modalName: null,
			selectApartmentBtnText: '无数据',
			networkList: [], //公寓列表
		}
	},
	onLoad: function(option) {
		_this = this;
		_this.UserInfoArray.user_wechat_apartment = option.openid;
	},
	mounted() {
		_this = this;
		var date = new Date();
		var seperator1 = "-";
		var year = date.getFullYear();
		var month = date.getMonth() + 1;
		var strDate = date.getDate();
		if (month >= 1 && month <= 9) {
			month = "0" + month;
		}
		if (strDate >= 0 && strDate <= 9) {
			strDate = "0" + strDate;
		}
		_this.UserInfoArray.date_entry = year + seperator1 + month + seperator1 + strDate;
		//获取所有网点
		/**......**/
		var allParams = {
			url: 'queryAllNetworkInfo',
			setUpUrl: true,
			type: 'get',
			sCallback: function(data) {
				if (data.code == 1) {
					uni.showToast({
						title: '获取网点数据异常',
						icon: 'none'
					})
				} else {
					if (data.data.length == 0) {
						uni.showToast({
							title: '网点数据为空',
							icon: 'none'
						})
					} else {
						_this.selectApartmentBtnText = "选择网点";
						_this.selectApartmentBtnDisabled = false;
						_this.networkList = data.data;
					}
		
				}
			}
		};
		_this.$base.request(allParams, _this);
	},
	methods: {
		//提交数据
		/**......**/
		formSubmitBind: function(e) {
			var rule = [{
					name: "user_id",
					checkType: "string",
					checkRule: "6,12",
					errorMsg: "工号应为6-12个字符"
				},
				{
					name: "user_name",
					checkType: "string",
					checkRule: "2,10",
					errorMsg: "姓名应为2-10个字符"
				},
				{
					name: "user_cardid",
					checkType: "string",
					checkRule: "18",
					errorMsg: "请输入18位身份证号码"
				},
				{
					name: "user_tel",
					checkType: "phoneno",
					checkRule: "",
					errorMsg: "请输入11位手机号码"
				},
				{
					name: "user_point",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "所属网点不可为空"
				},
				{
					name: "user_sex",
					checkType: "in",
					checkRule: "0,1",
					errorMsg: "请选择性别"
				},
				{
					name: "user_type",
					checkType: "in",
					checkRule: "0,1,2",
					errorMsg: "请选择新工类型"
				},
			];
			//进行表单检查
			var formData = e.detail.value;
			console.log(formData.user_id)
			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				var allParams = {
					url: 'bindUserInfoByUserInputs',
					setUpUrl: true,
					//data: JSON.stringify(formData),
					data:{
						date_entry: formData.date_entry,
						user_cardid: formData.user_cardid,
						user_id: formData.user_id,
						user_main: formData.user_main,
						user_name: formData.user_name,
						user_point: formData.user_point,
						user_sex: formData.user_sex,
						user_tel: formData.user_tel,
						user_type: formData.user_type,
						user_wechat_apartment: formData.user_wechat_apartment
					},
					type: 'get', //请求类型
					sCallback: function(data) {
						console.log(data);
						console.log(data.message);
						if (data.code == 0) {
							uni.redirectTo({
								url: '/pages/login/login',
								success() {
									uni.showToast({
										title: data.message,
										icon: 'none'
									});
								}
							});
						} else {
							uni.showModal({
								showCancel:false,
								title:'错误',
								content:data.message
							})
						}
					}
				};
				_this.$base.request(allParams,_this);
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
		},
		//获取临时工号
		/**......**/
		getTempUserId() {
			var allParams = {
				url: 'getTemporaryGenerationUserId', //地址为Config.restUrl拼接地址 
				setUpUrl: true, //不需要拼接请设置为false 
				type: 'post', //请求类型
				sCallback: function(data) {
					console.log(data);
					if (data.code == 0) {
						_this.UserInfoArray.user_id = data.message;
						_this.lsDisabled = true;
					} else {
						uni.showToast({
							title: '获取临时工号异常',
							icon: 'none'
						})
					}
				},
				eCallback: function() {}
			};
			_this.$base.request(allParams,_this);

		},
		//设置用户性别
		SetUserSex(e) {
			_this.UserInfoArray.user_sex = e.detail.value;
		},
		//设置用户用工类型
		SetUserType(e) {
			_this.UserInfoArray.user_type = e.detail.value;
		},
		//设置入职日期
		DateEntryChange(e) {
			_this.UserInfoArray.date_entry = e.detail.value;
		},
		
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		searchIcon(e) {
			let key = e.detail.value.toLowerCase();
			let list = _this.networkList;
			for (let i = 0; i < list.length; i++) {
				let a = key;
				let b = list[i].apa_name.toLowerCase();
				if (b.search(a) != -1) {
					list[i].isShow = true
				} else {
					list[i].isShow = false
				}
			}
			_this.networkList = list
		},
		clickItem(apa_name, apa_id) {
			_this.UserInfoArray.user_main = apa_id.split('-')[0];
			_this.UserInfoArray.user_point = apa_id.split('-')[1];
			_this.hideModal();
		}
	}
}
